<template xmlns="http://www.w3.org/1999/html">
  <div class="page-container w">
    <p v-show="!isShowProduct" class="err-tip">很抱歉，实在找不到您要的商品。</p>
    <div v-show="isShowProduct">
      <div class="intro-wrap">
        <div class="p-img-con">
          <div class="main-img-con">
            <img class="main-img" :src="mainImage" alt="name"/>
          </div>
          <ul class="p-img-list">
            <li class="p-img-item" v-for="url in subImages" :key="url">
              <img @mouseenter="changeMainImg(url)" class="p-img" :src="url"/>
            </li>
          </ul>
        </div>
        <div class="p-info-con">
          <h1 class="p-name">{{product.goodsName}}</h1>
          <p class="p-subtitle">{{product.marketName}}</p>
          <!--<div class="p-info-item p-price-con">-->
            <!--<span class="labelBig">采购价格：</span>-->
            <!--<span class="info">{{product.purchasePrice}}</span>-->
          <!--</div>-->
          <div class="p-info-item p-price-con">
            <span class="labelBig">出售价格：</span>
            <span class="info">{{product.sellPrice | formatMoney}}</span>
          </div>
          <div class="p-info-item p-price-con">
            <span class="labelBig">商品描述：</span>
            <span class="info">{{product.goodsIntroduce}}</span>
          </div>
          <div class="p-info-item">
            <span class="label">库存:</span>
            <span class="info">{{product.stock}}</span>
          </div>
          <div class="p-info-item p-count-con">
            <span class="label">数量:</span>
            <input class="p-count" :value="buyCount" readonly=""/>
            <span class="p-count-btn plus" @click="changeBuyCount('plus')">+</span>
            <span class="p-count-btn minus" @click="changeBuyCount('minus')">-</span>
          </div>
          <div class="p-info-item">
            <a class="btn cart-add" @click="addCart">加入购物车</a>
          </div>
        </div>
      </div>
      <div class="detail-wrap">
        <div class="detail-tab-con">
          <ul class="tab-list">
            <li class="tab-item active">详细描述</li>
          </ul>
        </div>
        <div class="detail-con">
          <div v-html="product.detail"></div>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
  export default {
    data() {
      return {
        isShowProduct: true,
        buyCount: 1,
        product: {},
        mainImage: '',
        subImages: []
      };
    },
    created() {
      this.loadDetail();
    },
    activated() {
      this.loadDetail();
      this.isShowProduct = true;
    },
    methods: {
      addCart() {
        let userCart = {
          productId: this.product.goodsId,
          quantity: this.buyCount,
          productName: this.product.goodsName,
          productPrice: this.product.sellPrice,
          mainImage: this.product.image,
          checked: true,
          productStock: this.product.stock
        };
        // Cookies.set('userCart',userCart);
        this.$axios.post(
          this.global.baseUrl + '/cart/addCart',
          userCart
        ).then((res) => {
          if (res.data.code === 200) {
            this.loadPage('result', {'type': 'user-cart'});
          } else {
            alert(res.data.msg);
          }
        }).catch(function (res) {
          alert(res);
        })
      },
      changeMainImg(img) {
        this.mainImage = img;
      },
      changeBuyCount(type) {
        if (!type) {
          alert('操作错误');
        }
        if (type === 'minus') {
          this.buyCount !== 1 ? this.buyCount-- : this.buyCount = 1;
        }
        if (type === 'plus') {
          this.buyCount !== this.product.stock ? this.buyCount++ : this.buyCount = this.product.stock;
        }
      },
      loadDetail() {
        let goodsId = this.getUrlParam('goodsId');
        if (!goodsId) {
          this.goHome();
          return;
        }
        let goodsInfo = {goodsId:goodsId};
        this.$axios.post(
          this.global.baseUrl + '/loadDetail',
          goodsInfo
        ).then((res) => {
          if (res.data.code === 200) {
            this.product = res.data.data;
            this.mainImage = this.product.image;
            let subImages = this.product.subImages;
            let array = subImages.split(',');
            for (let i = 0 ; i < array.length; i++) {
              this.subImages.push(array[i]);
            }
            // alert(this.subImages[2]);
          } else {
            this.isShowProduct = false;
          }
        }).catch(function (res) {
          alert(res);
        })
      }
    },
    components: {}
  };
</script>
<style rel="stylesheet/scss" lang="scss">
</style>



